.title {
  font-size: 1.2rem;
  font-weight: 700;
}

.dailyItem {
  width: 100%;
  height: 1rem;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  font-size: 0.7rem;
}

.smallIcon {
  width: 1.8rem;
}

.dailyItem .day {
  color: #212121;
  cursor: inherit;

  font-weight: 600;
  margin-left: 15px;
}

.description {
  cursor: inherit;
  margin-right: 15px;
  text-align: right;
}

.minMax {
  color: #757575;
}

.dailyDetails {
  display: flex;
  justify-content: space-between;
}

.detailsLeft {
  width: 30%;
}

.detailsRight {
  width: 30%;
}

.detailItem {
  display: flex;

  font-size: 14px;
  width: 100%;
  justify-content: space-between;
}

.detailItem label:first-child {
  color: #817c7c;
}

.detailItem label:last-child {
  color: #0c0a0a;
  font-weight: 600;
}
